<template>
    <!-- p style="margin: 5px;" :contenteditable="editMode" v-bind:style="`border: ${editMode ? 'dotted; black;' : 'none;'} border-width: ${editMode ? '1px;' : 'none;'}`" @blur="({ target: { textContent }}) => { updateFn(textContent) }">{{value}}</p -->
    <p style="margin: 5px;" :contenteditable="editMode" v-bind:style="`border: ${editMode ? 'dotted; black;' : 'none;'} border-width: ${editMode ? '1px;' : 'none;'}`" @blur="({ target: { textContent }}) => { updateFn(textContent) }">{{value}}
    </p>
</template>

<script lang="ts">
import { toRefs } from 'vue'
import { EditableFirestoreField } from '@/types/composed'
import updateProfileValue from '@/services/updateProfileValue'
import updateCardValue from '@/services/updateCardValue'

export default {
  name: 'EditableTextInput',
  props: {
    field: EditableFirestoreField,
    value: String,
    editMode: Boolean,
    postUid: String,
  },
  setup(props) {
    const updateFn = (textContent) => props.postUid ? updateCardValue(props.postUid, props.field, textContent, props.value) : updateProfileValue(props.field, textContent, props.value)
    return {
        ...toRefs(props),
        updateProfileValue,
        updateFn
    }
  }
}
</script>

