import { auth, getTimelineCollection, storageRef } from '@/firebase.config'



const deleteContentCard = async (postUid: string, hostedFileName?: string): Promise<boolean> => {
    const { currentUser } = auth
    const { uid } = currentUser || {}
    if (!uid) return false

    if (!confirm('⚠️ Are you sure?')) {
        return false
    }
    const timelineRef = getTimelineCollection(uid)

    if (!hostedFileName) {
        await timelineRef.doc(postUid).delete()
        return true
    }

    // Delete hosted file also
    const fileRef = storageRef.child(`${auth?.currentUser?.uid}/${hostedFileName}`)
    await Promise.all([
        fileRef.delete(),
        timelineRef.doc(postUid).delete()
    ])
    return true
}

export default deleteContentCard
