
import { ref } from 'vue'
import { BaseContentCard } from '@/types/BaseContentCard'
import updateCardValue from '@/services/updateCardValue'
import router from '@/router'

export default {
  props: {
    editMode: Boolean,
    userUid: String,
    postUid: String,
    cardBgColor: String,
    contentCard: BaseContentCard,
    onDelete: Function,
    isCardPreview: {
      type: Boolean,
      default: true
    },
    showPublicOptions: {
      type: Boolean,
      default: false
    },
  },
  setup (props) {
    const contentOptions = [
      ...props.contentCard.rawImages?.map(img => ({
        type: "image",
        previewImage: img
      })) || [],
      ...props.contentCard.rawVideos?.map(vid => ({
        type: "video",
        previewVideo: vid
      })) || [],
    ];
    if (props.contentCard.iFrameUrl) {
      contentOptions.push({
        type: 'iframe',
      })
    }

    let optIndex = 0;
    const hasMorePreviews = ref(contentOptions.length > 1);
    const changePreview = async () => {
      optIndex += 1;
      if (optIndex > contentOptions.length - 1) {
        optIndex = 0;
      }
      const { type, previewImage, previewVideo } = contentOptions[optIndex];
      
      await Promise.all([
        updateCardValue(props.postUid, "type", type),
        updateCardValue(
          props.postUid,
          type === "image" ? "previewImage" : "previewVideo",
          type === "image" ? previewImage : previewVideo,
          type === "image" ? props.previewImage : props.previewVideo,
        )
      ]);
      
    };

    const postUrl = `/#/u/${props.userUid}/p/${props.postUid}`
    const toPost = () => router.push(`/u/${props.userUid}/p/${props.postUid}`)

    return {
      changePreview,
      hasMorePreviews,
      postUrl,
      toPost
    };
  }
}
