import { UserProfile } from "@/types/UserProfile";
import { auth, profilesCollection } from '@/firebase.config'
import { useToaster } from '@ajmey/toaster/toaster'

const toaster = useToaster({animation: "appear", theme: "default", dismiss: 2000 })

const updateProfileValue = async (field: keyof UserProfile | string, newValue: string, prevValue?: string): Promise<void> => {
    if (prevValue && newValue.trim() === prevValue.trim()) {
        console.log('profile field unchanged', { field })
        return
    }

    const { currentUser } = auth
    const profileRef = profilesCollection.doc(currentUser?.uid)
    await profileRef.update(field, newValue.trim())
    toaster.success("Profile updated ✔️")
}

export default updateProfileValue
